#login-main {
  background-color: var(--third-color);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 90vh;
  display: flex;
}

#login-main-img1 {
  border: 2px solid #fff;
  border-radius: 15px;
  width: 300px;
  height: 200px;
  margin-top: 3%;
}

#login-main-form {
  color: var(--fourth-color);
  width: 70%;
  margin-top: 3%;
  margin-bottom: 0;
}

#login-main-form fieldset {
  border: none;
}

#login-password {
  background-color: var(--third-color);
  border: var(--fourth-color) 2px solid;
  color: #fff;
  box-sizing: border-box;
  outline: none;
  width: 60%;
  height: 30px;
  margin-top: 2%;
  padding: 1%;
  font-size: 110%;
}

#login-password:focus {
  background-color: #000;
}

#login-password-submit {
  background-color: var(--third-color);
  border: solid 2px var(--fourth-color);
  color: var(--fourth-color);
  cursor: pointer;
  padding: 1%;
}

#login-password-submit:hover {
  background-color: var(--fourth-color);
  color: var(--third-color);
}

#login-password-reset {
  background-color: var(--third-color);
  border: solid 2px var(--fourth-color);
  color: var(--fourth-color);
  cursor: pointer;
  padding: 1%;
}

#login-password-reset:hover {
  background-color: var(--fourth-color);
  color: var(--third-color);
}

#login-main-p1 {
  background-color: var(--fourth-color);
  color: #8b0000;
  border: 2px solid red;
  justify-content: center;
  width: auto;
  height: auto;
  margin-top: 1%;
  padding: 1%;
  display: none;
}
